<template>
  <v-card class="overflow-hidden">
    <v-app-bar absolute color="white" elevate-on-scroll scroll-target="#scrolling-techniques-7">
      <v-toolbar-title>Home</v-toolbar-title>

      <v-spacer></v-spacer>

      <v-btn icon>
        <v-icon>mdi-magnify</v-icon>
      </v-btn>

      <v-btn icon>
        <v-icon>mdi-heart</v-icon>
      </v-btn>

      <v-btn icon>
        <v-icon>mdi-dots-vertical</v-icon>
      </v-btn>
    </v-app-bar>
    <v-sheet
      id="scrolling-techniques-7"
      class="overflow-y-auto content-body-container"
      max-height="600"
    >
      <v-container style="height: 1500px;">
        <v-textarea
          v-model="postContent"
          no-resize
          filled
          auto-grow
          placeholder="What's happening?"
        ></v-textarea>
        <v-btn color="blue" class="white--text mb-3" @click="onSubmitPost">Post</v-btn>
        <v-divider></v-divider>
        <PostList v-if="posts" :posts="posts" />
      </v-container>
    </v-sheet>
  </v-card>
</template>

<script>
import { mapGetters, mapActions } from "vuex";
import PostList from "@/components/PostList";
export default {
  components: {
    PostList,
  },
  data: () => ({
    postContent: "",
  }),
  methods: {
    ...mapActions(["getPosts", "createPost"]),
    onSubmitPost() {
      if (this.postContent != "" && this.postContent.trim() != "") {
        const newPost = {
          creator: localStorage.getItem("userId"),
          content: this.postContent,
        };
        this.createPost(newPost).then((createdPost) => {
          this.getPosts();
          this.postContent = '';
        });
      }
    },
  },
  computed: {
    ...mapGetters(["posts"]),
  },
  created() {
    this.getPosts();
  },
};
</script>

